<template>
    <div>        
        <table class="table mt-4">
            <thead>
                <th width="120"></th>
                <th width="80">email</th>
                <th class="text-right"></th>
                <th width="120" class="text-right"></th>
                <th width="120"></th>
                <th width="80"></th>
                
            </thead>
            <tbody>
                <tr v-for="item in orders" :key="item.id">
                    <td>{{item.create_at}}</td>
                    <td>{{item.user.email}}</td>
                    <td class="text-right">
                        <div v-for="item in item.products" :key="item.id">{{item.product.title}}{{item.product.num}}{{item.product.unit}}</div>
                    </td>
                    <td class="text-right">{{item.total | currency}}</td>
                    <td>
                        <span v-if="item.is_paid" class="text-success"></span>
                        <span v-else class="text-danger"></span>
                    </td>
                    <td><button class="btn btn-outline-primary btn-sm" @click="openModal(item)"></button></td>
                    
                </tr>
            </tbody>
        </table>
        <Pagination :pageInfo="pagination" @pageChange="getOrderList"></Pagination>  


        <div class="modal fade" id="orderModal" tabindex="-1" role="dialog"
            aria-labelledby="exampleModalLabel" aria-hidden="true">
            <div class="modal-dialog modal-lg" role="document">
                <div class="modal-content border-0">
                <div class="modal-header bg-dark text-white">
                    <h5 class="modal-title" id="exampleModalLabel">
                    <span></span>
                    </h5>
                    <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                    <span aria-hidden="true">&times;</span>
                    </button>
                </div>
                <div class="modal-body">
                    <div class="row">
                    
                    <div class="col">                   
                        <div class="form-row">
                            <div class="form-group col">
                                <label for="title"></label>
                                <input type="text" class="form-control" id="title"
                                    placeholder="" >
                            </div>    
                            <div class="form-group col">
                                <label for="category"></label>
                                <input type="number" class="form-control" id="category"
                                placeholder="(%off)" >
                            </div>
                            <div class="form-group col">
                                <label for="price"></label>
                                <input type="unit" class="form-control" id="unit"
                                placeholder="yyyy-mm-dd" >
                            </div>
                            <div class="form-group col">
                                <label for="price"></label>
                                <input type="unit" class="form-control" id="unit"
                                placeholder="" >
                            </div>
                        </div>                        
                    </div>
                    </div>
                </div>
                <div class="modal-footer">
                    <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
                    <button type="button" class="btn btn-primary" @click="editList()"></button>
                </div>
                </div>
            </div>
        </div>
        
    </div>
</template>

<script>
import $ from 'jquery'
import Pagination from './pages/Pagination'
export default {
    components:{
        Pagination,
    },
    data(){
        return{
            orderId:"",
            orders:{
               products:{
                   
               },
               user:{
                   email:""
               }
           },
           pagination:{},
           tempOrder:{}
            
        }
    },
    methods:{
        getOrderList(page=1){
            const vm=this;
            vm.isLoading=true
            const api=`${process.env.APIPATH}/api/${process.env.APIKEY}/orders?page=${page}`;//'https://vue-course-api.hexschool.io/api/eric840906/products'
            this.$http.get(api).then((response) => {
            console.log(response.data)
            vm.orders=response.data.orders
            vm.pagination=response.data.pagination;
            console.log(vm.orders)
            vm.isLoading=false
            
            
            })    
        },
        editList(id){
            const vm=this;            
            const api=`${process.env.APIPATH}/api/${process.env.APIKEY}/order/${id}`;//'https://vue-course-api.hexschool.io/api/eric840906/products'
            this.$http.put(api,{data:vm.tempOrder}).then((response) => {
            console.log(response.data)
            vm.temporder=response.data        
            console.log(vm.temporder)
            
            
            
            })    
        },
        openModal(item){
            this.tempOrder=Object.assign({},item);
            $('#orderModal').modal('show')
            
        }
    },
    created(){
        this.getOrderList();
    }
}
</script>


