<template>
    <div>
        <div class="row flex-xs-column-reverse">
            <div class="col-md-4 md-mt-20">
                <h3>{{product.title}}</h3>
                <h4 style="text-align:end;">{{product.origin_price|currency}}</h4>
                <h5></h5>                
                <select name="" class="form-control" v-model="product.quantity">
                        <option selected disabled></option>
                        <option v-for="item in 10" :value="item" :key="item">{{item}}{{product.unit}}</option>
                        
                </select>
                <br>
                <h5>{{product.origin_price*product.quantity|currency}}</h5>
                <br>
                <h5></h5>
                <p>{{product.description}}</p>    
                
                <button class="addCart-btn" @click="addCart(product.id, product.quantity)"></button>
                

            </div>
            <div class="col-md-8 .container-fluid">
                <img :src="product.imageUrl" alt="" class="img-fluid">
            </div>
        </div>
        <Cart :cartInfo="cartList" :isloading="productStatus.loading" @refreshCart="getCart" @removeItem="removeCart"></Cart> 
    </div>
</template>

<script>
import $ from 'jquery'
import Navbar from './HomeNav'
import Cart from './Cart'

export default {
    components:{
        Navbar,
        Cart
    },
    data(){
        return{
            produtId:'',
            product:{
                
            },
            productStatus:{
                loading:''
            },
            cartList:{
                data:{
                    carts:[]
                }   //carts undefined()
            },
            coupon_code:"",
            
        }
    },
    methods:{
        getProduct(){
            const vm=this;                  
            const api=`${process.env.APIPATH}/api/${process.env.APIKEY}/product/${vm.productId}`;
            this.$http.get(api).then((response) => {
            console.log(response.data)            
            if(response.data.success){
                vm.product=response.data.product;
                vm.productStatus.loading=""
                
                
            }
            
            
            
            })               
            
        },
        getCart(){
            
            const vm=this;    
            vm.isLoading=true        
            const api=`${process.env.APIPATH}/api/${process.env.APIKEY}/cart`;//'https://vue-course-api.hexschool.io/api/eric840906/products'
            this.$http.get(api).then((response) => {
            console.log(response.data)
            vm.isLoading=false
            vm.cartList=response.data; 
            
            })               
            
        },
        addCart(id, qty=1){
            const vm=this;
            vm.productStatus.loading=id            
            const api=`${process.env.APIPATH}/api/${process.env.APIKEY}/cart`;
            const cart={
                product_id:id,
                qty
            }
            this.$http.post(api, {'data':cart}).then((response) => {
            console.log(response.data)
                if(response.data.success){
                    $('#productModal').modal('hide');
                    vm.productStatus.loading=""
                    console.log('')
                    this.getCart()
                    
                }else{
                    alert('')
                    vm.productStatus.loading=""
                }         
                                   
            })
            

        },
        removeCart(id){
            const vm=this;
            vm.productStatus.loading=id      
            let api=`${process.env.APIPATH}/api/${process.env.APIKEY}/cart/${id}`
            this.$http.delete(api).then((response) => {
                console.log(response.data)
                if(response.data.success){                            
                console.log('')
                this.getCart()
                vm.productStatus.loading=""                      
                
                }else{
                    console.log('')
                    vm.productStatus.loading=""   
                }
            })
        },
    },
    created(){
        this.productId=this.$route.params.productId   //$router.params.orderIdindex.jscustomercheckoutorderId(ID)
        console.log(this.productId)
        this.getProduct();
        this.getCart();
    }
    
}
</script>

<style lang="scss">

.addCart-btn{
    margin: 70px 0;
    width: 100%;
    border-style: none;
    transition: 0.3s all;
    
    &:hover{        
        background-color: #ffdf76;
        color: #292929;
    }
    &:focus{
        background-color: #292929;
        color: #ffdf76;
    }
}
@media(max-width: 425px){
    .md-mt-20{
        margin-top: 20px;
    }
    .flex-xs-column-reverse{
        flex-direction: column-reverse;
    }
}
</style>




