<template>
    <div>
        <div class="my-5 row justify-content-center">
            <form class="col-md-6" @submit.prevent="payConfirm">
                <table class="table">
                    <thead>
                        <th></th>
                        <th></th>
                        <th></th>
                    </thead>
                <tbody>
                    <tr v-for="item in orders.products" :key="item.id">
                        <td class="align-middle">{{ item.product.title }}</td>
                        <td class="align-middle">{{ item.qty }}/{{ item.product.unit }}</td>
                        <td class="align-middle text-right">{{ item.final_total }}</td>
                    </tr>
                </tbody>
                <tfoot>
                    <tr>
                        <td colspan="2" class="text-right"></td>
                        <td class="text-right">{{ orders.total }}</td>
                    </tr>
                </tfoot>
                </table>

                <table class="table">
                    <tbody>
                        <tr>
                        <th width="100">Email</th>
                        <td>{{ orders.user.email }}</td>
                        </tr>
                        <tr>
                        <th></th>
                        <td>{{ orders.user.name }}</td>
                        </tr>
                        <tr>
                        <th></th>
                        <td>{{ orders.user.tel }}</td>
                        </tr>
                        <tr>
                        <th></th>
                        <td>{{ orders.user.address }}</td>
                        </tr>
                        <tr>
                        <th></th>
                        <td>
                            <span v-if="!orders.is_paid" class="text-danger"></span>
                            <span v-else class="text-success"></span>
                        </td>
                        </tr>
                    </tbody>
                </table>
                <div class="text-right">
                    <button class="btn btn-danger" v-if="orders.is_paid==false"></button>
                </div>
            </form>
        </div>
    </div>
</template>

<script>
import $ from 'jquery'
export default {
    data(){
        return{
           orderId:'',
           orders:{
               products:{
                   
               },
               user:{}
           }

        }

    },
    methods:{                                            
                  
        getOrder(){
            const vm=this;                     
            const api=`${process.env.APIPATH}/api/${process.env.APIKEY}/order/${vm.orderId}`;//'https://vue-course-api.hexschool.io/api/eric840906/products'
            this.$http.get(api).then((response) => {
            console.log(response.data)
            vm.orders=response.data.order
            console.log(vm.orders)
            })
        },
        payConfirm(){
            const vm=this;                   
            const api=`${process.env.APIPATH}/api/${process.env.APIKEY}/pay/${vm.orderId}`;//'https://vue-course-api.hexschool.io/api/eric840906/products'
            this.$http.post(api).then((response) => {
            console.log(response.data)
                if(response.data.success){
                    this.getOrder()
                }
            
            
            })

        }
        
    },
    created(){
        this.orderId=this.$route.params.orderId   //$router.params.orderIdindex.jscustomercheckoutorderId(ID)
        console.log(this.orderId)
        this.getOrder()

    }
    
        
    
    
}
</script>


