<template>
    <div>
        <loading :active.sync="isLoading"></loading>
        <div class="text-right mt-4">
            <button class="btn btn-outline-success btn-sm" type="button" @click="openModal('isNew')"></button>
        </div>
        <table class="table mt-4">
            <thead>
                <th width="120"></th>
                <th></th>
                <th width="120" class="text-right"></th>
                <th width="120" class="text-right"></th>
                <th width="120">/</th>
                <th width="80"></th>
                <th width="80"></th>
            </thead>
            <tbody>
                <tr v-for="(item) in products" :key="item.id">
                    <td>{{item.category}}</td>
                    <td>{{item.title}}</td>
                    <td class="text-right">{{item.origin_price | currency}}</td>
                    <td class="text-right">{{item.price | currency}}</td>
                    <td>
                        <span v-if="item.isEnabled" class="text-success"></span>
                        <span v-else class="text-danger"></span>
                    </td>
                    <td><button class="btn btn-outline-primary btn-sm" @click="openModal('isEdit',item)" ></button></td>
                    <td><button class="btn btn-danger btn-sm" @click="openModal('isDelete',item)"></button></td>
                </tr>
            </tbody>
        </table>
        
            <Pagination :pageInfo="pagination" @pageChange="getProducts"></Pagination>  
            <!-- <nav aria-label="Page navigation example">
                <ul class="pagination justify-content-center">
                    <li class="page-item" :class="{'disabled': !pagination.has_pre}">
                    <a class="page-link" href="#" aria-label="Previous" @click.prevent="getProducts(pagination.current_page-1)">
                        <span aria-hidden="true">&laquo;</span>
                    </a>
                    </li>
                    <li class="page-item" v-for="page in pagination.total_pages" :key="page" :class="{'active':pagination.current_page==page}">
                        <a class="page-link" href="#" @click.prevent="getProducts(page)">{{page}}</a>
                    </li>
                    
                    <li class="page-item" :class="{'disabled': !pagination.has_next}">
                    <a class="page-link" href="#" aria-label="Next" @click.prevent="getProducts(pagination.current_page+1)">
                        <span aria-hidden="true">&raquo;</span>
                    </a>
                    </li>
                </ul>
            </nav> -->

            <div class="modal fade" id="addProduct" tabindex="-1" role="dialog"
                aria-labelledby="exampleModalLabel" aria-hidden="true">
                <div class="modal-dialog modal-lg" role="document">
                    <div class="modal-content border-0">
                    <div class="modal-header bg-dark text-white">
                        <h5 class="modal-title" id="exampleModalLabel">
                        <span></span>
                        </h5>
                        <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                        <span aria-hidden="true">&times;</span>
                        </button>
                    </div>
                    <div class="modal-body">
                        <div class="row">
                        <div class="col-sm-4">
                            <div class="form-group">
                            <label for="image"></label>
                            <input type="text" class="form-control" id="image"
                                placeholder="" v-model="tempProduct.imageUrl">
                            </div>
                            <div class="form-group">
                            <label for="customFile"> 
                                <i class="fas fa-spinner fa-pulse" v-if="status.isUploading"></i>
                            </label>
                            <input type="file" id="customFile" class="form-control"
                                ref="files" @change="uploadImage()">
                            </div>
                            <img img="" :src="tempProduct.imageUrl"
                            class="img-fluid" alt="" >
                        </div>
                        <div class="col-sm-8">
                            <div class="form-group">
                            <label for="title"></label>
                            <input type="text" class="form-control" id="title"
                                placeholder="" v-model="tempProduct.title">
                            </div>

                            <div class="form-row">
                            <div class="form-group col-md-6">
                                <label for="category"></label>
                                <input type="text" class="form-control" id="category"
                                placeholder="" v-model="tempProduct.category">
                            </div>
                            <div class="form-group col-md-6">
                                <label for="unit"></label>
                                <input type="unit" class="form-control" id="unit"
                                placeholder="" v-model="tempProduct.unit">
                            </div>
                            </div>

                            <div class="form-row">
                            <div class="form-group col-md-6">
                            <label for="origin_price"></label>
                                <input type="number" class="form-control" id="origin_price"
                                placeholder="" v-model="tempProduct.origin_price">
                            </div>
                            <div class="form-group col-md-6">
                                <label for="price"></label>
                                <input type="number" class="form-control" id="price"
                                placeholder="" v-model="tempProduct.price">
                            </div>
                            </div>
                            <hr>

                            <div class="form-group">
                            <label for="description"></label>
                            <textarea type="text" class="form-control" id="description"
                                placeholder="" v-model="tempProduct.description"></textarea>
                            </div>
                            <div class="form-group">
                            <label for="content"></label>
                            <textarea type="text" class="form-control" id="content"
                                placeholder="" v-model="tempProduct.content"></textarea>
                            </div>
                            <div class="form-group">
                            <div class="form-check">
                                <input class="form-check-input" type="checkbox"
                                id="is_enabled" v-model="tempProduct.isEnabled"
                                :true-value="1" :false-value="0">
                                <label class="form-check-label" for="is_enabled">
                                
                                </label>
                            </div>
                            </div>
                        </div>
                        </div>
                    </div>
                    <div class="modal-footer">
                        
                        <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
                        <button type="button" class="btn btn-primary" @click="updateProduct"></button>
                        
                    </div>
                    </div>
                </div>
                </div>
                <div class="modal fade" id="delProductModal" tabindex="-1" role="dialog"
                aria-labelledby="exampleModalLabel" aria-hidden="true">
                <div class="modal-dialog" role="document">
                    <div class="modal-content border-0">
                    <div class="modal-header bg-danger text-white">
                        <h5 class="modal-title" id="exampleModalLabel">
                        <span></span>
                        </h5>
                        <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                        <span aria-hidden="true">&times;</span>
                        </button>
                    </div>
                    <div class="modal-body">
                         <strong class="text-danger"></strong> {{tempProduct.title}} ()
                    </div>
                    <div class="modal-footer">
                        <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
                        <button type="button" class="btn btn-danger" @click="deleteProduct()"
                        ></button>
                    </div>
                    </div>
                </div>
            </div>
    </div>
</template>

<script>

import $ from "jquery" //vue$
import Pagination from './Pagination'

export default {  //export
    components: {
        Pagination,
    },
    data(){
        return{
            pagination:{},
            products:[],
            tempProduct:{

            },
            isNew:false,
            isEdit:false,
            isDelete:false,
            isLoading:false,
            status:{
                isUploading:false
            }
        }
    },
    methods:{
        getProducts(page){
            
            const vm=this;
            vm.isLoading=true
            const api=`${process.env.APIPATH}/api/${process.env.APIKEY}/products?page=${page}`;//'https://vue-course-api.hexschool.io/api/eric840906/products'
            this.$http.get(api).then((response) => {
            console.log(response.data)
            vm.isLoading=false
            vm.products=response.data.products; 
            vm.pagination=response.data.pagination;
            console.log(vm.pagination.current_page)
            })               
            
        },
        openModal(status, item){  //javascriptmodal
            switch(status){
                case 'isNew':
                this.tempProduct={};
                this.isNew=true;
                $('#addProduct').modal('show');
                break;
                case 'isEdit':
                this.tempProduct=Object.assign({},item);
                $('#addProduct').modal('show');
                break;
                case 'isDelete':
                this.tempProduct=item;
                $('#delProductModal').modal('show');
                break;

            }

            ///(switch)
            // if(isNew){
            //     this.tempProduct={};
            //     this.isNew=true
            // }else{
            //     this.tempProduct=Object.assign({},item); //Object.assign()item
            //     this.isNew=false
            // }
            // $('#addProduct').modal('show')
           
            

            
            
        },
        updateProduct(){
            const vm=this;
            let api=`${process.env.APIPATH}/api/${process.env.APIKEY}/admin/product`;//'https://vue-course-api.hexschool.io/api/eric840906/products'
            let httpMedthod='post';
            if(!vm.isNew){
                httpMedthod='put'
                api=`${process.env.APIPATH}/api/${process.env.APIKEY}/admin/product/${vm.tempProduct.id}`
                                            //vm.tempProductdataapi
                this.$http[httpMedthod](api, {data: vm.tempProduct}).then((response) => {
                
                }) 
            }
            this.$http[httpMedthod](api, {data: vm.tempProduct}).then((response) => {
            console.log(response.data)
            if(response.data.success){
                $('#addProduct').modal('hide')
                vm.getProducts()
            }else{
                $('#addProduct').modal('hide')
                vm.getProducts()
                alert("")
            }
            //vm.products=response.data.products; 
            }) 
            
            
                 
        },
        uploadImage(){//: 1(uploadedImage) 2.formData 3.formData(API) 4.
            console.log(this)//console(this.$ref.files.files[0])
            const uploadedImage = this.$refs.files.files[0];
            const vm=this;
            vm.status.isUploading=true
            const formData= new FormData();  //https://developer.mozilla.org/en-US/docs/Web/API/FormData/FormData         WEB API
            formData.append("file-to-upload",uploadedImage) //(API)
            const url = `${process.env.APIPATH}/api/${process.env.APIKEY}/admin/upload`;
            this.$http.post(url, formData,{
                headers:{
                    'Content-Type':'multipart/form-data'
                }
            }).then((response)=>{
                console.log(response.data)
                vm.status.isUploading=false
                if(response.data.success){
                    //vm.tempProduct.image=response.data.imageUrl  //gettersetter$set
                    vm.$set(vm.tempProduct,'imageUrl',response.data.imageUrl)
                    this.$bus.$emit('message:push','','success');
                    
                }else
                {
                    console.log(response.data)
                    this.$bus.$emit('message:push','','success');
                }
            })
        },
        //(switchopenModal())
        // deletConfirm(item){
        //     this.tempProduct=Object.assign({},item);
        //     $('#delProductModal').modal('show')
        // },
        deleteProduct(){
            
            const vm=this;
            let api=`${process.env.APIPATH}/api/${process.env.APIKEY}/admin/product/${vm.tempProduct.id}`
            this.$http.delete(api).then((response) => {
                console.log(response.data)
                if(response.data.success){
                console.log('')
                vm.getProducts()
                $('#delProductModal').modal('hide') 
                this.$bus.$emit('message:push',`:${vm.tempProduct.title}`,'danger');
            }
            })
            
            
        }
    },
    created() {
        this.getProducts();
        
    },
    
}
</script>


