import Vue from 'vue'
import Router from 'vue-router'
import Dashboard from '@/components/Dashboard'
import Login from '@/components/pages/Login'
import Products from '@/components/pages/Products'
import CustomerOrder from '@/components/CustomerOrder'
import Coupon from '@/components/pages/Coupon'
import PayPage from '@/components/PayPage'
import CustomerCheckout from '@/components/CustomerCheckout'
import OrderList from '@/components/OrderList'
import Home from '@/components/Home'
import ShopPage from '@/components/ShopPage'
import ShoppingMain from '@/components/ShoppingMain'
import ShopProduct from '@/components/ShopProduct'
import PaymentMake from '@/components/PaymentMake'
import PaymentPage from '@/components/PaymentPage'
import PaymentConfirm from '@/components/PaymentConfirm'
import CouponJackpot from '@/components/CouponJackpot'
import Contact from '@/components/Contact'
import Storeinfo from '@/components/Storeinfo'
import CommentForm from '@/components/CommentForm'
import CommentManage from '@/components/CommentManage'




Vue.use(Router)

export default new Router({
  routes: [
    {
        //
        path:'*',
        redirect: '/login'
    },
    {
      path: '/login',
      name: 'login',
      component: Login
    },
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/commentform',
      name: 'CommentForm',
      component: CommentForm
    },
    {
      path: '/shoppage',
      name: 'ShopPage',
      component: ShopPage,
      children:[
        {
          path: 'shoppingmain', 
          name: 'ShoppingMain',
          component: ShoppingMain,
          
        },        
        {
          path: 'product/:productId', 
          name: 'ShopProduct',
          component: ShopProduct,
          
        },
        
      ]
    },
    {
      path: '/couponjackpot',
      name: 'CouponJackpot',
      component: CouponJackpot
    },
    {
      path: '/contact',
      name: 'Contact',
      component: Contact
    },
    {
      path: '/storeinfo',
      name: 'Storeinfo',
      component: Storeinfo
    },
    {
      path: '/paymentpage',
      name: 'PaymentPage',
      component: PaymentPage,
      children:[
        {
          
          path: 'paymentmake',
          name: 'PaymentMake',
          component: PaymentMake,
        },        
        {
          path: 'paymentconfirm/:orderId',   ///:orderIdapiIDnav guardlogin
          name: 'PaymentConfirm',
          component: PaymentConfirm,
          
        },
      ]
    },
    {
      path: '/admin',
      name: 'Dashboard',
      component: Dashboard,       
      meta: { requiresAuth: true },//
      children:[
        {
        path: 'products',   //
        name: 'Products',
        component: Products,
        meta: { requiresAuth: true }
        },
        {
          path: 'coupon',
          name: 'coupon',
          component: Coupon,
          meta: { requiresAuth: true }
        },
        {
          path: 'orderlist',
          name: 'OrderList',
          component: OrderList,
          meta: { requiresAuth: true }
        },  
          
      ]
    
    },    
    {
      path: '/dashboard',
      name: 'Dashboard',
      component: Dashboard,           
      children:[
        {
        path: 'customerorder',   //
        name: 'CustomerOrder',
        component: CustomerOrder,
        
        },
        {
          path: 'paypage',   //
          name: 'PayPage',
          component: PayPage,
          
        },
        {
          path: 'customercheckout/:orderId',   ///:orderIdapiIDnav guardlogin
          name: 'CustomerCheckout',
          component: CustomerCheckout,
          
        },
        {
          path: 'commentmanage',   ///:orderIdapiIDnav guardlogin
          name: 'CommentManage',
          component: CommentManage,
          
        },
          
      ]
    
    },
    
    
  ]
})



