<template>
    <div>
        <Alert></Alert>
        <Navbar></Navbar>
        <main role="main">

            <section class="jumbotron text-center" style="border-radius: 0; padding-bottom: 104px;; margin-bottom:0; backgroundImage:url(https://images.unsplash.com/photo-1573991288363-aa2067bc6a73?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1267&q=80); background-size: cover; background-position: center;">
                
                <div class="page-title">
                    <h1 style="font-family: 'Tangerine', cursive; font-size: 4.5rem; color: cornsilk;">Choose Your Favourites</h1><br>
                    <h1 class="lead"></h1>
                </div>
                
            </section>
            
            <div class="album py-5 bg-light">
                <div class="container">
                    <router-view></router-view>
                </div>
                <!-- <Cart :cartInfo="cartList" :isloading="productStatus.loading" @refreshCart="getCart" @removeItem="removeCart"></Cart> -->
                
                <GoTop></GoTop>
            </div>
            
        </main>
    </div>
</template>

<script>
import $ from 'jquery'
import Navbar from './HomeNav'
import Cart from './Cart'
import GoTop from './GoTop'
import Alert from './Alert'
import Footer from './Footer'



export default {
    components:{
        Navbar,
        Cart,
        GoTop,
        Alert,
        Footer
        
    },
    data(){
        return{
            products:[],
            product:{},
            productStatus:{
                loading:''
            },
            cartList:{
                data:{
                    carts:[]
                }   //carts undefined()
            },
            coupon_code:"",
            visibility:'all',
            
        }
    },
    methods:{
        getProducts(page=1){  //page=1page1
            
            const vm=this;
            vm.isLoading=true
            const api=`${process.env.APIPATH}/api/${process.env.APIKEY}/products`;//'https://vue-course-api.hexschool.io/api/eric840906/products'
            this.$http.get(api).then((response) => {
            console.log(response.data)
            vm.isLoading=false
            vm.products=response.data.products; 
            console.log(vm.products)
            
            })               
            
        },
        getProduct(id){
            const vm=this;
            vm.productStatus.loading=id            
            const api=`${process.env.APIPATH}/api/${process.env.APIKEY}/product/${id}`;
            this.$http.get(api).then((response) => {
            console.log(response.data)            
            if(response.data.success){
                vm.product=response.data.product;
                vm.productStatus.loading=""
                vm.$router.push(`/${vm.product.id}`)
                
            }
            
            
            
            })               
            
        },
        getCart(){
            
            const vm=this;    
            vm.isLoading=true        
            const api=`${process.env.APIPATH}/api/${process.env.APIKEY}/cart`;//'https://vue-course-api.hexschool.io/api/eric840906/products'
            this.$http.get(api).then((response) => {
            console.log(response.data)
            vm.isLoading=false
            vm.cartList=response.data; 
            
            })               
            
        },
        addCart(id, qty=1){
            const vm=this;
            vm.productStatus.loading=id            
            const api=`${process.env.APIPATH}/api/${process.env.APIKEY}/cart`;
            const cart={
                product_id:id,
                qty
            }
            this.$http.post(api, {'data':cart}).then((response) => {
            console.log(response.data)
                if(response.data.success){
                    $('#productModal').modal('hide');
                    vm.productStatus.loading=""
                    this.$bus.$emit('message:push','','success');
                    this.getCart()
                    
                }else{
                    alert('')
                    vm.productStatus.loading=""
                }         
                                   
            })
            

        },
        removeCart(id){
            const vm=this;
            vm.productStatus.loading=id      
            let api=`${process.env.APIPATH}/api/${process.env.APIKEY}/cart/${id}`
            this.$http.delete(api).then((response) => {
                console.log(response.data)
                if(response.data.success){                            
                console.log('')
                this.getCart()
                vm.productStatus.loading=""                      
                
                }else{
                    console.log('')
                    vm.productStatus.loading=""   
                }
            })
        },
        productFilter(value){
            const vm=this
            
            vm.tempProducts = vm.products.filter(function(item,index,array){
            return item.category==value
            })            

            console.log(vm.tempProducts)

        }
    },
    created(){
        this.getProducts();
        this.getCart();
        

        
    },
    mounted(){
        $('.carttrigger').click(function (e) { 
            e.preventDefault();
            
        });
        $('.cartframe').click(function (e) { 
            e.preventDefault();
            e.stopPropagation();
            $('.cartlist').addClass('show');
             
        });
        $('body').click(function (e) { 
            e.preventDefault();
            $('.cartlist.show').removeClass('show');
            
        });
        
        
    },
    computed:{
        filterProducts: function(){
            if(this.visibility=='all'){
                return this.products;
            }
            else if(this.visibility==''){
                let newFilter=[]
                this.products.forEach(element=>{
                    if(element.category==''){
                        newFilter.push(element)
                    }
                });
                return newFilter
            }
            else if(this.visibility==''){
                let newFilter=[]
                this.products.forEach(element=>{
                    if(element.category==''){
                        newFilter.push(element)
                    }
                });
                return newFilter
            }
            else if(this.visibility==''){
                let newFilter=[]
                this.products.forEach(element=>{
                    if(element.category==''){
                        newFilter.push(element)
                    }
                });
                return newFilter
            }
        }
    }  
}
</script>

<style lang="scss">

@import url(https://fonts.googleapis.com/css2?family=Tangerine:wght@700&display=swap);
body{
    font-family: 'cwTeXYen', sans-serif;
}

.jumbotron{
    height: 400px;
    margin-top: 22px;
    h1{
        display: inline-block;
    }
}
.border-right{
    border-right: 1px solid black;
}

.badge-secondary {
    background-color: #292929;
    color: #ffdf76;
    
}

.card-footer{
    padding: 0;
    .shop-btn{
        width: 50%;    
        border: none;        
        border-radius: 0;
        transition:0.3s;
        &:hover{
            background-color: #292929;
            color: #ffdf76;
        }
        
    }
}
.page-title{
    color: black;
    padding: 96px 0;
    background-image: radial-gradient(#f0b3b3a8, #ffffff00);
}


.top-select{
    display: none;
    .top-select-item{                
        
        .active{
            border: 1px solid black;
            
        }
    }
        
    
}


.sidebar .nav-item .nav-link.active{
    color: #ffdf76; // for example
}
.sidebar {  
  top: 0;
  bottom: 0;
  left: 0;
  z-index: 100; /* Behind the navbar */
  padding: 48px 0 0; /* Height of navbar */
  box-shadow: inset -1px 0 0 rgba(0, 0, 0, .1);
  
  .nav-item{
    
    color: #ffdf76;
    &:hover{
        background-color: #ffdf76;
    }
    .active{
        background-color: #292929;
        .nav-link a{
            color: #ffdf76;
        }
    }
    
}
}







@media (max-width: 767.98px) {
  .sidebar {
    top: 5rem;
  }
  .top-select{
      display: block;
  }
}

.sidebar-sticky {
  position: relative;
  top: 0;
  height: calc(100vh - 48px);
  padding-top: .5rem;
  overflow-x: hidden;
  overflow-y: auto; /* Scrollable contents if viewport is shorter than content. */
}

@supports ((position: -webkit-sticky) or (position: sticky)) {
  .sidebar-sticky {
    position: -webkit-sticky;
    position: sticky;
  }
}

.sidebar .nav-link {
  font-weight: 500;
  color: #333;
}

.sidebar .nav-link .feather {
  margin-right: 4px;
  color: #999;
}



.sidebar .nav-link:hover .feather,
.sidebar .nav-link.active .feather {
  color: inherit;
}

.sidebar-heading {
  font-size: .75rem;
  text-transform: uppercase;
}
    
</style>


