<template>
    <div>
        <div class="text-right mt-4">
            <button class="btn btn-outline-success btn-sm" type="button" @click="openModal('isNew')"></button>
        </div>
        <table class="table" style="margin: 30px auto; width:70%">
            <thead>
                <th></th>                
                <th></th>
                <th></th>
                <th></th>
                <th></th>
                <th class="text-right"></th>
                <th></th>
            </thead>
            <tbody>
                <tr v-for="item in coupons" :key="item.id">
                <td class="align-middle">
                    <button type="button" class="btn btn-outline-danger btn-sm" @click="deleteCoupon(item.id)">
                    <i class="far fa-trash-alt"></i>
                    </button>
                </td>
                <td class="align-middle">
                    {{ item.title }}                    
                </td>
                <td class="align-middle">{{item.percent}}%off</td>
                <td>
                    <span v-if="item.is_enabled" class="text-success"></span>
                    <span v-else class="text-danger"></span>
                </td>
                <td class="align-middle">{{item.code}}</td>
                <td class="align-middle text-right">{{ item.due_date }}</td>
                <td><button class="btn btn-outline-primary btn-sm" @click="openModal('isEdit',item)" ></button></td>
                </tr>
            </tbody>                                   
            
        </table>
        <Pagination :pageInfo="pagination" @pageChange="getCoupon"></Pagination>  

        <div class="modal fade" id="couponModal" tabindex="-1" role="dialog"
            aria-labelledby="exampleModalLabel" aria-hidden="true">
            <div class="modal-dialog modal-lg" role="document">
                <div class="modal-content border-0">
                <div class="modal-header bg-dark text-white">
                    <h5 class="modal-title" id="exampleModalLabel">
                    <span></span>
                    </h5>
                    <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                    <span aria-hidden="true">&times;</span>
                    </button>
                </div>
                <div class="modal-body">
                    <div class="row">
                    
                    <div class="col">                   
                        <div class="form-row">
                            <div class="form-group col">
                                <label for="title"></label>
                                <input type="text" class="form-control" id="title"
                                    placeholder="" v-model="tempCoupon.title">
                            </div>    
                            <div class="form-group col">
                                <label for="category"></label>
                                <input type="number" class="form-control" id="category"
                                placeholder="(%off)" v-model="tempCoupon.percent">
                            </div>
                            <div class="form-group col">
                                <label for="price"></label>
                                <input type="unit" class="form-control" id="unit"
                                placeholder="yyyy-mm-dd" v-model="tempCoupon.due_date">
                            </div>
                            <div class="form-group col">
                                <label for="price"></label>
                                <input type="unit" class="form-control" id="unit"
                                placeholder="" v-model="tempCoupon.code">
                            </div>
                        </div>

                        <div class="form-row">                       
                        
                        <div class="form-check">
                            <input class="form-check-input" type="checkbox" id="is_enabled" v-model="tempCoupon.is_enabled" :true-value="1" :false-value="0">
                            <label class="form-check-label" for="is_enabled">
                            
                            </label>
                        </div>
                        </div>
                    </div>
                    </div>
                </div>
                <div class="modal-footer">
                    <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
                    <button type="button" class="btn btn-primary" @click="addCoupon"></button>
                </div>
                </div>
            </div>
        </div>
            
    </div>
</template>

<script>
import $ from 'jquery'
import Pagination from './Pagination'
export default {
    components: {
        Pagination,
    },
    data(){
        return{
            coupons:[],
            tempCoupon:{},
            isNew:false,
            pagination:{}
            
        }
    },
    methods:{
        getCoupon(page=1){
            
            const vm=this;
            
            const api=`${process.env.APIPATH}/api/${process.env.APIKEY}/admin/coupons?page=${page}`;//'https://vue-course-api.hexschool.io/api/eric840906/products'
            this.$http.get(api).then((response) => {
            console.log(response.data)
            vm.coupons=response.data.coupons
            vm.pagination=response.data.pagination
            console.log(vm.pagination)
            
            })               
            
        },
        addCoupon(){
            const vm=this;
            let api=`${process.env.APIPATH}/api/${process.env.APIKEY}/admin/coupon`;//'https://vue-course-api.hexschool.io/api/eric840906/products'
            let httpMedthod='post';
            this.due_date=new Date(vm.due_date).getTime();
            if(!vm.isNew){
                httpMedthod='put'
                api=`${process.env.APIPATH}/api/${process.env.APIKEY}/admin/coupon/${vm.tempCoupon.id}`
                                            //vm.tempCoupondataapi
                this.$http[httpMedthod](api, {data: vm.tempCoupon}).then((response) => {
                
                }) 
            }
            this.$http[httpMedthod](api, {data: vm.tempCoupon}).then((response) => {
            console.log(response.data)
            if(response.data.success){
                $('#couponModal').modal('hide')
                vm.getCoupon()
            }else{
                $('#couponModal').modal('hide')
                vm.getCoupon()
                alert("")
            }
            //vm.products=response.data.products; 
            }) 
        },
        deleteCoupon(id){
            const vm=this;
            let api=`${process.env.APIPATH}/api/${process.env.APIKEY}/admin/coupon/${id}`
            this.$http.delete(api).then((response) => {
                console.log(response.data)
                if(response.data.success){
                console.log('')
                vm.getCoupon()
                $('#delProductModal').modal('hide')                
            }
            })
        },
        openModal(status,item){
            switch(status){
                case "isNew":
                this.tempCoupon={};
                this.isNew=true;
                $('#couponModal').modal('show');
                break;
                case "isEdit":
                this.tempCoupon=Object.assign({},item);                
                $('#couponModal').modal('show');
                break;        
            }
        }

    },
    created(){
        this.getCoupon()
    }
}
</script>

<style>
    input::-webkit-outer-spin-button,
    input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
    }
</style>


